*,
*::before,
*::after {
	box-sizing: border-box;
}
html {
	font-size: 0.520835vw;
	font-style: normal;
	font-weight: normal;
	-webkit-animation: bugfix infinite 1s;
	line-height: 1.2;
	margin: 0;
	padding: 0;
}


body {
	font-style: normal;
	font-weight: normal;
	-webkit-animation: bugfix infinite 1s;
	line-height: 1.2;
	margin: 0;
	padding: 0;
}

input,
textarea {
	-webkit-animation: bugfix infinite 1s;
	line-height: inherit;
	margin: 0;
	padding: 0;
}
a {
	color: unset;
}
a,
a:hover {
	text-decoration: none;
}

button,
a {
	cursor: pointer;
}

button,
input,
a,
textarea {
	outline: none;
	font: inherit;
	&:focus {
		outline: none;
	}
	&:active {
		outline: none;
	}
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font: inherit;
	margin: 0;
	padding: 0;
}
p {
	margin-top: 0;
	margin-bottom: 0;
}

button {
	border: none;
	color: inherit;
	font: inherit;
	text-align: inherit;
	padding: 0;
	background-color: inherit;
}
ul {
	padding: 0;
	margin: 0;
}

ul li {
	margin: 0;
	padding: 0;
	list-style: none;
}

.container {
	width: 144rem;
	margin: 0 auto;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

input[type="number"] {
	-moz-appearance: textfield;
}

@media (max-width: 48em) {
	html {
		font-size: 5px;
		font-size: 1.5625vw;
		font-size: calc((100 / 320) * 5vw); // где 320 это ширина моб версии макета
		-webkit-text-size-adjust: none;
	}

	body {
		-webkit-text-size-adjust: none;
	}

	.container {
		padding: 0 3rem; // в моб версии отступ от края задаем для всех контейнеров, а там где не нужно можем точечно убрать
		width: 100%;
	}
}
h1 {
	font-size: 3.2rem;
	font-weight: bold;
	margin: 4rem 0;
}